.page {
  box-sizing: border-box;
  font-family: "Manrope", sans-serif;
  font-size: 16px;
  line-height: 22.4px;
  color: $main-text-color;
  scroll-behavior: smooth;


  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;
    transition: all 0.3s;
    transform: translateX(-100%);
    pointer-events: none;
  }

  &__menu:target {
    opacity: 1;
    transform: translateX(0);
    pointer-events: all;
  }
}
.page:has(.page__menu:target) {
  overflow: hidden;
}
