.menu {
  overflow: auto;
  box-sizing: border-box;
  height: 100vh;
  background-color: #F9F9F9;
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    margin-bottom: 32px;

    position: sticky;
    top: 0;
    z-index: 1;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    display: block;
    color: inherit;
    text-decoration: none;
    margin-bottom: 16px;
  }

  &__call-to-order {
    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    display: block;
    color: inherit;
    text-decoration: none;
    padding-bottom: 7px;
    border-bottom: 1px solid  $main-text-color;
    width: fit-content;
  }

  &__logo {
    height: 16px;

    @include onTablet {
      height: 24px;
    }
  }
}
